.landing {
    display: flex;
    color: white;
    background: transparent;
    transition: width 2s;
    margin:10px 10px 10px 0px;

    height:90vh;
    width: 100%;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    position: relative;

}

@media only screen and (max-height: 1000px) {
    .landing{
        height: fit-content;
    }
}
.text-color {
    color: white;
}

.dev-text {
    font-size: 30px;
    text-decoration: underline;
    text-decoration-color: #FF5F6D;
    color: black;

    position: relative;
    display: inline-block;
    font-weight: 800;
    overflow: hidden;
}

.soft-text {
    text-align: center;
    font-size: 20px;

}

.landing-text-content {
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: fit-content;
    margin: 10px;
    width: 100%;
}

.landing-all-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: fit-content;
    color: black;
    font-weight: bolder;
    transition: width 2s;
    margin: 10px auto;
}

.landing-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 10px;
    align-items: center;
}
.connect-text{
    font-size: 20px;
}
.image-landing {
    width: 250px;
    border-radius: 50%;
    border: 1px orangered solid;
    transition: width 2s;
    box-shadow: 3px 3px #fff;
    transition-duration: 1s,1s;
    transition-property: box-shadow,width;
}

.image-landing:hover{
    border: 0px #FF5F6D solid;
    box-shadow: 8px 8px #379683;
    width: 200px;
}

.name-font {
    color: white;
    background: #FF5F6D;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #FFC371, #FF5F6D);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #FFC371, #FF5F6D); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    font-size: 40px;
    font-weight: bold;
    text-shadow: 3px 3px #102b25;
    transition-duration: 1s,1s;
    transition-property: text-shadow,background;
    margin: 10px;
}

.name-font:hover{
    text-shadow: 6px 3px #379683;
    font-weight: normal;
    background: #FF5F6D;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to left, #FFC371, #FF5F6D);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #FFC371, #FF5F6D); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.social-media{
    display:flex;
    flex-direction: row;
    justify-content: space-around;
    border: 1px solid #FFC371;
    border-radius: 10px;
    margin-top: 10px;
}

.social-media-item{
    cursor: pointer;
    padding: 10px;
    border-radius: 10px;

}

.social-media-item:hover{
    background: #FFC371;
}

@media only screen and (max-width: 1200px) {
    .name-font{
        font-size:40px;
    }

    .dev-text {
        font-size: 25px;
    }

    .image-landing {
        width: 250px;

    }

    .image-landing:hover{
        width: 300px;
    }

}
@media only screen and (max-width: 400px) {
    .name-font{
        font-size:30px;
    }

    .dev-text {
        font-size: 20px;
    }


    .image-landing {
        width: 200px;
    }

    .image-landing:hover{

        width: 250px;
    }
}




.right-content {
    display: flex;
    width: 100%;
    height: fit-content;
    align-self: center;
    margin: 10px;
}

.right-text-content {
    text-align: right;
}